body {
  background: rgb(7, 9, 15);
  overflow-x: hidden;
  font-family: "Times New Roman", Times, serif;
  color: white;
}

iframe {
  position: absolute;
  inset: 0;
  height: 120vh;
  width: 100vw;
  z-index: -1;
  overflow-x: hidden;
}

.container {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  align-items: center;
  justify-content: center;
  overflow-x: hidden;
}

.deck {
  position: absolute;
  width: 300px;
  height: 200px;
  will-change: transform;
  display: flex;
  align-items: center;
  justify-content: center;
  touch-action: none;
}

.deck > div {
  background-color: rgb(18, 46, 40);
  background-size: auto 85%;
  background-repeat: no-repeat;
  background-position: center center;
  width: 45vh;
  max-width: 250px;
  height: 85vh;
  max-height: 400px;
  will-change: transform;
  border-radius: 10px;
  box-shadow: 0 12.5px 100px -10px rgb(9, 21, 9),
    0 10px 10px -10px rgba(76, 124, 93, 0.3);
}
